* {
  box-sizing: border-box;
}

body {
  color: #333;
  background-color: #f4f4f9;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

#container {
  justify-content: space-between;
  width: 100%;
  margin: 0 auto;
}

#sortAndFilterDiv {
  justify-content: space-around;
  gap: 20px;
  display: flex;
}

#header {
  text-align: center;
  background-color: #f4f4f4;
  flex-direction: column;
  height: 200px;
  margin-bottom: 20px;
  padding: 20px;
  box-shadow: 0 2px 4px #0000001a;
}

#selectFormDivSort, #selectFormDivFilter {
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  display: flex;
}

#sortTimeDiv, #sortByTitleDiv, #filterCategoryDiv, #filterMemberDiv {
  flex-direction: column;
  align-items: center;
  width: 200px;
  margin-bottom: 5px;
  display: flex;
}

#formContainer {
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 20px;
  width: 100%;
  margin-top: 20px;
  padding: 10px;
  display: flex;
}

#addMemberForm, #addAssignmentForm {
  text-align: center;
  background-color: #fff;
  border-radius: 8px;
  width: 100%;
  max-width: 500px;
  margin-top: 20px;
  padding: 20px;
  box-shadow: 0 4px 8px #0000001a;
}

h1 {
  color: #333;
  margin-bottom: 20px;
  font-size: 24px;
}

input[type="text"], select {
  box-sizing: border-box;
  border: 2px solid #ddd;
  border-radius: 5px;
  width: 100%;
  margin: 10px 0;
  padding: 10px;
  font-size: 16px;
  transition: border-color .3s;
}

input[type="text"]:focus, select:focus {
  border-color: #4caf50;
  outline: none;
}

#assignmentDescription {
  width: 100%;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  width: 100%;
  padding: 12px;
  font-size: 18px;
  font-weight: bold;
  transition: background-color .3s;
}

button[type="submit"]:hover {
  background-color: #45a049;
}

select[multiple] {
  height: 120px;
}

.team-member {
  background: #e7f9e7;
  border-radius: 8px;
  margin: 8px 0;
  padding: 12px;
  font-size: 16px;
  box-shadow: 0 2px 4px #0000001a;
}

.board {
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 30px;
  width: 100%;
  margin-top: 30px;
  padding: 10px;
  display: flex;
}

.column {
  background: #fff;
  border-radius: 10px;
  width: 250px;
  height: 100%;
  padding: 15px;
  box-shadow: 0 6px 8px #0000001a;
}

.column h2 {
  text-align: center;
  color: #fff;
  background: #007bff;
  border-radius: 8px;
  margin-bottom: 15px;
  padding: 12px;
  font-size: 20px;
}

.assignment {
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin: 10px 0;
  padding: 15px;
  box-shadow: 0 2px 5px #0000001a;
}

.assignment h4 {
  color: #333;
  margin-bottom: 8px;
  font-size: 18px;
}

.assignment p {
  color: #555;
  margin: 5px 0;
  font-size: 14px;
  line-height: 1.5;
}

#assignToBtn, #doneBtn, #deleteBtn {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  width: 100%;
  padding: 12px;
  font-size: 18px;
  font-weight: bold;
  transition: background-color .5s;
}

#assignToBtn, #doneBtn:hover {
  background-color: #45a049;
}

#deleteBtn:hover {
  background-color: #f44336;
}

#modal {
  background-color: #00000080;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  top: 0;
  left: 0;
  box-shadow: 0 4px 8px #0000001a;
}

.modal-content {
  text-align: center;
  background-color: #fff;
  border-radius: 8px;
  width: 100%;
  max-width: 400px;
  padding: 20px;
}

.modal-content ul {
  padding: 0;
  list-style-type: none;
}

.modal-content li {
  margin: 10px 0;
}

.assign-btn {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  padding: 10px;
}

.assign-btn:hover {
  background-color: #45a049;
}

.close-modal {
  color: #fff;
  cursor: pointer;
  background-color: #f44336;
  border: none;
  border-radius: 5px;
  margin-top: 20px;
  padding: 10px;
}

.done {
  background-color: #d3ffd3;
  text-decoration: line-through;
}

.red-button {
  color: #fff;
  cursor: pointer;
  background-color: red;
  border: none;
  padding: 10px 15px;
}

.red-button:hover {
  background-color: #8b0000;
}

@media (width <= 768px) {
  #addMemberForm, #addAssignmentForm {
    width: 100%;
    max-width: 450px;
  }

  .board {
    justify-content: center;
    gap: 20px;
  }
}

@media (width <= 480px) {
  #addMemberForm, #addAssignmentForm {
    width: 100%;
    max-width: 350px;
    margin-bottom: 20px;
  }

  .team-member, .column {
    width: 100%;
    margin: 10px 0;
  }

  .board {
    flex-direction: column;
    gap: 10px;
  }
}
